<template>
    <div class="instrument-box posi-re flex flexbox-v plr15"  >
        <div class="score bor-ra6 text-center p10" :class="{'score-show':isstart && !dialog.visible}">
            <div class="f15">本次剩余时间</div>
            <div class="pp">{{timer.txt || t_txt}}</div>
        </div>
        <div class="ptb15 default">
            <div class="pb10">授权状态：
                <span>0次</span>，
                <span>1次45分</span>
            </div>
            <div>剩余次数：0次</div>
        </div>
        <div class="flex flexbox flex-vcenter flex-hcenter">
            <a class="bor-ra100 default p50 pointer f25 big-btn big-btn-start" @click="start_btn()" :class="{'big-btn-stop':isstart}" v-text="!isstart ? '开始':'结束'">
            </a>
        </div>
        <div class="pb15">
            <div class="pb15 default">ID：343434343434</div>
            <div class="pb10">
                <a class="bor-ra6 ptb8 plr15 pointer bgc-bg2" @click="dialog_wifi.visible =true">联网设置</a>
                <span class="cnote pl10 f14">网络链接：未连接</span>
            </div>
        </div>
        <div class="qrcode-img">
            <img width="90" src="../assets/qrcode.png" alt="">
        </div>
        <transition>
            <div class="confirm-box" :class="{'active':dialog.visible}">
                <div class="inblock box p20 flexbox flex-vcenter flex-hcenter">
                    <h2 class="">是否确定本次使用？</h2>
                    <div class="pt20 flexbox flex-hbetween">
                        <a class="bor-ra6 ptb8 plr20 pointer calm-bg default" @click="hide_dialog()">确定</a>
                        <a class="bor-ra6 ptb8 plr20 pointer bgc-bg2" @click="dialog.visible = false">取消</a>
                    </div>
                </div>
            </div>
        </transition>
        <el-dialog custom-class="wifi-box" :show-close="false" :visible.sync="dialog_wifi.visible">
            <div>
                <div class="flexbox flex-vcenter pb15">
                    <div class="pr25 flex">选择网络 (SSID)：</div>
                    <div class="flex2">
                        <el-select v-model="dialog_wifi.name" placeholder="请选择">
                            <el-option v-for="item in 5" :key="item" :label="item" :value="item">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <div class="flexbox flex-vcenter">
                    <div class="pr25 flex">网络密码 (PSWD)：</div>
                    <div class="flex2">
                    <!--<el-input class="flex" type="textarea" @focus="show($event)" autosize v-model="dialog_wifi.pwd">
                        </el-input>-->
                        <input autosize="true" autocomplete="off"  type="text" class="flex el-textarea__inner"  @focus="show" data-layout="normal" />
                    </div>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialog_wifi.visible = false">取 消</el-button>
                <el-button type="primary" @click="dialog_wifi.visible = false">连 接</el-button>
            </span>
        </el-dialog>
        <vue-touch-keyboard v-if="keyboard.visible" :accept="accept" :layout="'normal'" :cancel="hide" :input="keyboard.input" />
    </div>
</template>
<style lang="scss">
    @import './yiqi.scss';
</style>  

<script>
import Vue from 'vue'
import { Button, Dialog, Input, Select, Option } from 'element-ui'
import LightController from '../services/LightController.js';
import VueTouchKeyboard from "vue-touch-keyboard";
import style from "vue-touch-keyboard/dist/vue-touch-keyboard.css";

Vue.use(VueTouchKeyboard);
Vue.component(Button.name, Button)
Vue.component(Dialog.name, Dialog)
Vue.component(Input.name, Input)
Vue.component(Select.name, Select)
Vue.component(Option.name, Option)

function nums(n) {
    return n > 9 ? n + '' : '0' + n
}
const lc = new LightController();
export default {
    data() {
        return {
            isstart: false,
            dialog: {
                visible: false
            },
            dialog_wifi: {
                visible: false,
                pwd: '',
                name: ''
            },
            timer: {
                tid: null,
                txt: '',
                btime:10*60,
                time: 10 * 60 // 分钟
            },
            keyboard:{
                visible:false,
                input:null
            },

        }
    },
    computed: {
        t_txt() {
            let t = this.timer.time;
            return nums(parseInt(t / 60)) + ':' + nums((t % 60));
        },
        time_over() {
            return this.timer.tid === null && this.timer.time === 0;
        }
    },
    watch: {
        time_over() {
            // this.start_btn();
        }
    },
    mounted() {
        window.qq = this;
        window.cc = lc;
    },
    methods: {
        hide_dialog() {
            this.dialog.visible = false;
            setTimeout(() => {
                this.isstart = true;
                // lc.start();
                this.timer.time = this.timer.btime;
                this.startTimer();
            }, 300)
        },
        start_btn() {
            if (!this.isstart) {
                this.dialog.visible = true;
            } else {
                this.dialog.visible = false;
                this.isstart = false;
                lc.stop();
                this.stopTimer();
            }
            console.log('start_btn'); 
        },
        stopTimer() {
            this.timer.tid && clearInterval(this.timer.tid);
            this.timer.tid = null;
        },
        startTimer() { 
            if (this.timer.tid) return;
            this.timer.tid = setInterval(() => {
                let t = this.timer.time = this.timer.time - 1;
                this.timer.txt = nums(parseInt(t / 60)) + ':' + nums((t % 60));
                if (t == 0 && this.timer.tid) {
                    this.stopTimer();
                }
            }, 1000)
            this.timer.txt = nums(parseInt(this.timer.time / 60)) + ':' + nums((this.timer.time % 60));
        },
        show(e) { 
         console.log(e);
          this.keyboard.input = e.target;
          if (!this.keyboard.visible)
            this.keyboard.visible = true
        },

        hide() {
          this.keyboard.visible = false;
        },
        accept(text) {
          console.log('accept',text);
          this.hide();
        },
    }
}
</script>


