function log(v) {
  console.log(v);
}

class LightController {
  constructor(time) {
    this.waiting = false;
    this.io2 = false; // 电磁阀
    this.io1 = false; // 泵的状态 关闭的时候延迟15s
    this.btn = false; // 按钮的状态
    this.tid = null;
    this.time = time || 15000;
    this.rpio = window["rpio"] || {};
    this.init();
  }

  init() {
    let rpio = this.rpio;
    const pollcb = pin => {
      let state = rpio.read(pin) ? "released" : "pressed";
      if (state == "pressed") {
        this.press();
      } else if (state == "released") {
        this.unpress();
      }
      console.log("Button event on P%d (button currently %s)", pin, state);
    };

    this.rpio.poll(15, pollcb);
  }
  stop() {
    // 关闭机器
    // 按钮状态
    this.btn = false;
    // 机器状态
    if (this.io2) {
      this.io2 = false;
      this.rpio.write(38, this.rpio.HIGH);
      log("stop io2 ...");
    }
    if (this.io1) {
      this.io1 = false;
      this.rpio.write(40, this.rpio.HIGH);
      log("stop io1 ...");
    }
  }
  start() {
    this.btn = true; // 按钮状态
    // 机器状态
    if (!this.io1) {
      log("start io1 ... ");
      this.io1 = true;
      this.rpio.write(40, this.rpio.LOW);
    }

    if (!this.io2) {
      log("start io2 ... ");
      this.io2 = true;
      this.rpio.write(38, this.rpio.LOW);
    }
  }
  press() {
    if (!this.btn) {
      this.start();
      return;
    }
    log("你踩下了...");
    this.tid && clearTimeout(this.tid);
    this.start();
    this.waiting = false;
  }
  unpress() {
    if (!this.btn) {
      return;
    }
    log("你松脚了");
    this.handle_unpress();
  }
  handle_start() {
    // 按钮启动
    if (!this.btn) {
      log("开始使用");
      this.btn = true;
    }
  }
  handle_close() {
    if (this.btn) {
      this.stop();
    } else {
      log("不可以用了");
    }
  }

  handle_unpress() {
    if (this.io1) {
      this.io1 = false;
      this.rpio.write(40, this.rpio.HIGH);
      log("stop io1 .");
    }
    if (this.waiting) {
      log("未到15s，您又踩了");
      log("不关闭pump io2");
      
      return;
    }
    log("开始等待15s ...");
    this.waiting = true;
    this.tid = setTimeout(() => {
      if (this.io2 && this.waiting === true) {
        this.io2 = false;
        log("结束15s stop io2");
        this.rpio.write(38, this.rpio.HIGH);
        this.tid && clearTimeout(this.tid);
      }
    }, this.time);
  }
}

export default LightController;



